<template>
  <div class="showoff col">
    <div class="vocabularybox">
      <div class="inputbox">
        <textarea
          v-model="inputbox"
          placeholder="Enter or paste vocbulary here"
        >
        </textarea>
      </div>
      <div class="searchbox">
        <button><i class="fas fa-search"></i>search</button>
      </div>
      <div class="outputbox">
        <div class="vocabularyOutput">
          <h4>apple</h4>
          <h5>蘋果</h5>
          <h6>n.</h6>
          <h6>US [ˈæp.ɫ̩]</h6>
          <div class="level">A1</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Vocabulary",
  data() {
    return {
      inputbox: "",
    };
  },
};
</script>



